.hero{
    background: rgb(87,81,195);
    background: linear-gradient(to right, #d9a7c7, #fffcdc);
    display: flex;

}
.hero-left{
    flex: 1;
    display : flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}
.hero-left h2{
    color: black;
    font-size: 26px;
    font-weight: 600;
}
.hero-left p{
    color: black;
    font-size: 100px;
    font-weight: 700;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap:  20px;

}
.hero-hand-icon img{
    width: 105px;
}

.hero-latest-btn{
    display : flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500;
}
.hero-right{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}


@media (max-width: 1280px){
    .hero-left{
        padding-left: 100px;
    }
    .hero-left p{
        font-size: 60px;

    }
    .hero-left h2{
        font-size: 22px;

    }
    .hero-hand-icon img{
        width: 80px;

    }
    .hero-latest-btn {
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
        
 
    }
    .hero-right img{
        width: 500px;

    }
}
@media (max-width: 1024px){
    .hero-left{
        padding-left: 80px;
    }
    .hero-left p{
        font-size: 50px;

    }
    .hero-left h2{
        font-size: 22px;

    }
    .hero-hand-icon img{
        width: 65px;

    }
    .hero-latest-btn {
      
        width: 250px;
        height: 50px;
        
        font-size: 16px;
        
 
    }
    .hero-right img{
        width: 400px;

    }

}

@media (max-width: 800px){
    .hero-left{
        padding-left: 30px;
    }
    .hero-left p{
        font-size: 40px;

    }
    .hero-left h2{
        font-size: 16px;

    }
    .hero-hand-icon img{
        width: 50px;

    }
    .hero-latest-btn {
        width: 175px;
        height: 40px;
        
        font-size: 13px;
        
 
    }
    .hero-right img{
        width: 300px;

    }

}
@media (max-width: 500px){
    .hero{
        flex-direction: column;
        padding-bottom: 95px;
        padding-top: 100px;
    }
    .hero-right img{
        display: none;
    }
    .hero-left h2{
        font-size: 18px;
    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-hand-icon{
        width: 55px;
    }
    .hero-latest-btn{
        width: 200px;
        height: 45px;
        font-size:14px ;
    }
    .hero-left{
        padding-left: 43px;
    }

}