.productdisplay{
    display: flex ;
    margin: 0px 170px;

}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.productdisplay-img-list img{
    height: 163px;
}
.productdisplay-right{
    margin: 0px 70px;
    display : flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 700;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-prices{
    display: flex;
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-family: 700;
}
.productdisplay-right-prices-old{
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-prices-new{
    color: red;
}
.productdisplay-right-size h1{
    margin-top: 55px;
    color: #656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-sizes{
    display: flex;
    margin: 30px 0px;
    gap: 60px;
}
.productdisplay-right-sizes div{
    border: 3px solid #dcc5c5;
    padding: 14px;
}
.productdisplay-right button{
    padding: 20px 40px ;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background: #FF4141;
    margin-bottom: 40px;
    border: none;
    outline : none;
    cursor: pointer;
}
.prodproductdisplay-right-category{
    margin-top: 10px;
}
.prodproductdisplay-right-category span{
    font-weight: 600;
}
.productdisplay-main-img{

}


@media (max-width: 1280px){
    .productdisplay{
        margin: 0px 60px;
    }
    .productdisplay-left{
        gap: 10px;
    }
    .productdisplay-img-list{
        gap: 10px;
    }
    .productdisplay-img-list img{
        height: 120px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 510px;
    }
    .productdisplay-right{
        margin: 0px 30px;
    }
    
    .productdisplay-right h1{
        font-size: 22px;
    }
    .productdisplay-right-stars{
        gap: 3px;
        font-size: 13px;
    }
    .productdisplay-right-prices{
        margin: 10px 0px;
        font-size: 18px;
    }
    .prodproductdisplay-right-description{
        font-size: 13px;
    }
    .productdisplay-right button{
        width: 150px;
        padding: 15px 0px;
        margin-bottom: 20px;
    }
    .prodproductdisplay-right-category{
        margin-top: 5px;
    }

}
@media (max-width: 1024px){
    .productdisplay{
        margin: 0px 30px;
    }
    .productdisplay-left{
        gap: 5px;
    }
    .productdisplay-img-list img{
        height: 80px;
    }
    .productdisplay-main-img{
        height: 350px;
    }
    .productdisplay-right{
        margin: 0px 30px;
    }
    
    .productdisplay-right h1{
        font-size: 18px;
    }
    .productdisplay-right-stars{
        gap: 3px;
        font-size: 13px;
    }
    .productdisplay-right-prices{
        margin: 10px 0px;
        font-size: 18px;
    }
    .prodproductdisplay-right-description{
        font-size: 12px;
    }
    .productdisplay-right button{
        width: 120px;
        padding: 10px 0px;
        margin-bottom: 10px;
        font-size: 14px;
    }
    .prodproductdisplay-right-category{
        
        font-size: 12px;
    }
    .productdisplay-right-sizes{
        gap: 30px;

    }
    .productdisplay-right-sizes div{
        padding: 10px;
    }





}
@media (max-width: 800px){
    .productdisplay{
        margin: 0px 10px;
    }
    .productdisplay-left{
        gap: 7px;
        flex-direction: column-reverse;
        align-items: center;
    }
    .productdisplay-img-list{
        flex-direction: row;
        gap: 6px;
    }
    .productdisplay-img-list img{
        height: 70px;
    }
    .productdisplay-main-img{
        height: 300px;
    }
    .productdisplay-right{
        margin: 0px 30px;
    }
    
    .productdisplay-right h1{
        font-size: 14px;
    }
    .productdisplay-right-stars{
        gap: 3px;
        font-size: 13px;
    }
    .productdisplay-right-stars img{
        width: 12px;
    }
    .productdisplay-right-prices{
        margin: 10px 0px;
        font-size: 18px;
    }
    .productdisplay-right-description{
        font-size: 10px;
    }
    .productdisplay-right button{
        width: 100px;
        font-size: 12px;
    }
    .prodproductdisplay-right-category{
        
        font-size: 10px;
    }
    .productdisplay-right-sizes{
       margin-top: 10px;

    }
    .productdisplay-right-sizes div{
        padding: 7px;
    }
    .productdisplay-right-size h1{
        margin-top: 10px;
    }


}
@media (max-width: 500px){
    .productdisplay{
        flex-direction: column;
        gap: 20px;
    }
    .productdisplay-left{
        flex-direction: row;
        gap: 10px;
        margin: auto;
    }
    .productdisplay-img-list{
        flex-direction: column;
        gap: 8px;
    }
    .productdisplay-right{
        margin: 5px;
    }
    .productdisplay-right h1{
        margin-top: 15px;
        font-size: 20px;
    }
    .productdisplay-right-description{
        font-size: 13px;
    }
    .productdisplay-right-sizes{
        gap: 15px;

    }
    .productdisplay-right button{
        width: 130px;
        padding: 12px 0px;
        font-size: 15px;
    }
    .prodproductdisplay-right-category{
        font-size: 16px;
    }

}