.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 2px 1px -2px black;
    align-items: center;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: black;
    font-weight: 800;
    font-size: 40px;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #9b8466;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background:#FF4141 ;
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid orange;
    border-radius: 75px;
    color: yellow;
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
    

}
.nav-login-cart button:active{
    background:rgb(242, 242, 242);
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;  
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}
.nav-dropdown{
    display: none;
}
@media (max-width: 1280px) {
    .navbar{
        padding: 12px 50px;

    }
    .nav-logo img{
        width: 40px;

    }
    .nav-logo p{
        font-size: 25px;

    }
    .nav-menu{
        gap: 30px;
        font-size: 16px;

    }
    .nav-login-cart{
        gap:30px;

    }
    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
    
}
@media (max-width: 1024px) {
    .navbar{
        padding: 12px 30px;

    }
    
    .nav-menu{
        gap: 25px;
        font-size: 14px;

    }
    .nav-login-cart img{
        width: 30px;

    }
    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }
    .nav-cart-count{
        width:18px ;
        height:18px ;
    }

}
@media (max-width:800px) {
    .navbar{
        padding: 10px 0px;
    }
    .nav-dropdown{
        display: block;
        width: 30px;
        transition: 0.5s;

    }
    .nav-menu{
        display: none;
        height: 80px;
        width: 94%;
        background-color: white;
        justify-content: center;
        position: absolute;
        top: 56px;

       
    }
    .nav-menu-visible{
        display: flex;
    }
}
@media (max-width: 500px) {
    .navbar{
        padding: 8px 23px;
        gap: 0;
    }
    .nav-logo{
        transform: scale(0.8);
    }
    .nav-menu{
        height: 70px;
        width: 89%;
    }
    .nav-logo p{
        font-size: 24px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
    
}