.offers{
    width: 65%;
    height: 60vh;
    display : flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background-image: linear-gradient(to right, #fffbd5, #b20a2c);

}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;

}
.offers-left h1{
    color: rgb(32, 31, 31);
    font-size: 54px;
    font-weight: 600;
    font-style: italic;

}
.offers-left p{
    color:rgb(32, 31, 31);
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: rgb(192, 53, 192);
    border: none;
    margin-top: 30px;
    cursor: pointer;
    font-size: 22px;
    color: white;
    font-weight: 500;
}

@media (max-width: 1280px){
    .offers{
        padding: 0px 80px;
        margin-bottom: 120px;
    }
    .offers-left h1{
        font-size: 40px;
    }
    .offers-left p{
        font-size: 18px;
    }
    .offers-right img{
        width: 300px;
    }
    .offers-left button{
        width: 220px;
        height: 50px;
        font-size: 18px;
    }
    .offers-right{
        padding-top: 30px;
    }
}
@media (max-width: 1024px){
    .offers{
        padding: 0px 60px;
        margin-bottom: 80px;
        height: 50vh;
    }
    .offers-left h1{
        font-size: 30px;
    }
    .offers-left p{
        font-size: 16px;
    }
    .offers-right img{
        width: 200px;
    }
    .offers-left button{
        width: 160px;
        height: 40px;
        font-size: 16px;
    }
    .offers-right{
        padding-top: 10px;
    }

}
@media (max-width: 800px){
    .offers{
        margin-bottom: 60px;
       
    }
    .offers-left h1{
        font-size: 22px;
    }
    .offers-left p{
        font-size: 13px;
    }
    .offers-right img{
        width: 180px;
    }
    .offers-left button{
        width: 140px;
        height: 30px;
        font-size: 12px;
    }
    .offers-right{
        padding-top: 10px;
    }

}
@media (max-width: 500px){
    .offers{
        margin-bottom: 50px;
        height: 25vh;
       
    }
    .offers-left h1{
        font-size: 16px;
    }
    .offers-left p{
        font-size: 10px;
    }
    .offers-right img{
        width: 120px;
    }
    .offers-left button{
        width: 120px;
        height: 25px;
        font-size: 10px;
        margin-top: 12px;
    }
    .offers-right{
        padding-top: 10px;
    }

}